var React = require('react');
var ReactDOM = require('react-dom');

import "slick-carousel/slick/slick.scss";
import "slick-carousel/slick/slick-theme.scss";
import { Card, CardActions, CardHeader, CardMedia, CardTitle, CardText } from 'material-ui/Card';
import { Link } from 'react-router'
import space from "../../../Images/space.png"
import blockchain from "../../../Images/ethereum.png"

import Slider from "react-slick";

class SimpleSlider extends React.Component {
  render() {
    var settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1
      
    };
    return (
      <Slider {...settings}>
        <div>
        <Link to="BlockchainInfo">
          <div className="bigCard" id="bigCardA">
            <CardMedia
              overlay={<CardTitle title="Building better blockchain apps" subtitle="Ethereum block chains" />}
            >
              <img src={blockchain} alt="" style={{ 'height': '30em' }} />
            </CardMedia>
          </div>
        </Link>
        </div>
        <div>
        <Link to="BlockchainInfo">
        <div className="bigCard" id="bigCardB">
          <CardMedia
            overlay={<CardTitle title="Preparing for the future" subtitle="Science and Innovation" />}>
            <img src={space} alt="" style={{ 'height': '30em' }} />

          </CardMedia>
          </div>
          </Link>
        </div>

      </Slider>
    );
  }
}

export default SimpleSlider



